@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI Variable Text","Segoe UI","Meiryo",system-ui,ui-sans-serif,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji";
}

html, body, #root {
  height: 100%;
}

*:not([style="white-space: pre;"] > span) {
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}

#root {
  display: flex;
  flex-wrap: nowrap;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace !important;
}

$dividerColor: rgb(50, 50, 50);

#lua-sidebar {
  left: 0;
  overflow-y: scroll;
  height: 100%;
  background-color: rgb(29, 29, 29);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  border-right: 1px solid $dividerColor;
  transition: all 0.2s;
  width: 300px;

  &::-webkit-scrollbar {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  #lua-sidebar {
    width: 0px;
  }
  .lua-wrapper {
    width: 100% !important;
    padding-left: 10px !important;
  }
  .lua-container {
    padding-left: 10px !important;
  }

  .lua-alert {
    & > div {
      width: calc(100% - 50px) !important;
    }
  }
  #lua-sample-container {
    height: 300px;
  }
}

h1,h2,h3,h4,h5,h6 {
  font-weight: 500;
}

.lua-divider {
  background-color: $dividerColor;
  &.vertical {
    height: 100%;
    width: 1px;
  }
  &.horizontal {
    width: 100%;
    height: 1px;
  }
  &.margin {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.lua-list {
  padding: 7px;
  transition: all .2s;
  
  & > .lua-listitem-container {
    padding-bottom: 0px;
    padding-top: 0px;
    display: block;
    position: relative;
    width: 100%;

    &.active {
      background: rgb(50, 50, 50);
    }
  }

  &.background {
    background-color: rgba(20, 20, 20, 0.5);
    padding: 0px;
    height: 0px;
    overflow: hidden;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}

.lua-button {
  padding: 7px 30px 7px 30px;
  width: 100%;
  outline: none;
  border-radius: 3px;
  background: transparent;
  border: none;
  color: white;
  transition: 0.2s all;
  font-weight: 400;
  font-size: 15px;
  position: relative;
  overflow: hidden;

  &:hover {
    background: rgba(255, 255, 255, 0.12);
  }

  &.active {
    background: rgb(50, 50, 50);
  }
}

.lua-ripple {
  width: 100%;
}

.lua-container {
  position: relative;
  color: white;
  padding: 10px;
  padding-left: 50px;
  max-height: 100%;
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.lua-small {
  font-size: 13px;
  color: white;
}

.lua-info {
  font-size: 13px;
  color: rgb(75, 75, 75)
}

.lua-rows {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-top: 10px;
  border-radius: 3px;
  padding: 10px;
}

.lua-column {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  border-radius: 3px;
  padding: 10px;
}

.lua-card {
  background-color: rgb(35, 35, 35);
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin: 0 auto;
  gap: 0;
  &.fullwidth {
    width: 100%;
  }
  flex: 1 0 calc(50% - 10px);
}

.lua-warning {
  display: block;
  background-color: rgba(255, 0, 0, 0.3);
  color: white;
  padding: 0.5rem;
  border-radius: 3px;
  margin: 10px;
  margin-left: 3px;
  margin-right: 3px;
}

.lua-topbar {
  top: 0;
  padding: 5px;
  background-color: rgb(45, 45, 45);
  border-radius: 3px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.lua-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  color: white;
  padding: 10px;
  padding-left: 50px;
  max-height: 100%;
  overflow-y: auto;
  width: calc(100% - 160px - 240px);
}

$lightDark: #101010;

.lua-Inputbase {
  border-radius: 3px;
  background-color: $lightDark;
  outline: none;
  border: none;
  color: white;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI Variable Text","Segoe UI","Meiryo",system-ui,ui-sans-serif,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji" !important;
  font-weight: 400;
  position: relative;
  width: 100%;
  padding: 0;
  padding-top: 7px;
  padding-bottom: 7px;

  &.text {
    transition: all .1s;
    &:focus {
      background-color: #161616;
    }
  }

  &.button {
    cursor: pointer;

    &:hover {
      background-color: #131313;
    }
  }
}

// Alert 
.lua-alert {
  position: fixed;
  width: 100%;
  bottom: 0;
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  
  & > div {
    position: relative;
    background-color: rgb(55, 55, 55);
    display: flex;
    align-items: center;
    text-align: start;
    padding: 7px;
    border-radius: 4px;
    color: white;
    min-width: calc(100% / 7);
    font-weight: 300;

    &.success {
      background-color: rgb(0, 160, 55);
    }
  }
}

#lua-sample-container {
  position: relative;
  & > img {
    width: 100%;
    height: 100%;
    filter: blur(6px);
  }
  & > .lua-sample-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}