@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#121212;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI Variable Text","Segoe UI","Meiryo",system-ui,ui-sans-serif,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji"}html,body,#root{height:100%}*:not([style="white-space: pre;"]>span){font-family:'Spoqa Han Sans Neo', 'sans-serif'}#root{display:flex;flex-wrap:nowrap}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace !important}#lua-sidebar{left:0;overflow-y:scroll;height:100%;background-color:#1d1d1d;display:flex;flex-direction:column;align-items:center;color:white;border-right:1px solid #323232;transition:all 0.2s;width:300px}#lua-sidebar::-webkit-scrollbar{display:none}@media only screen and (max-width: 600px){#lua-sidebar{width:0px}.lua-wrapper{width:100% !important;padding-left:10px !important}.lua-container{padding-left:10px !important}.lua-alert>div{width:calc(100% - 50px) !important}#lua-sample-container{height:300px}}h1,h2,h3,h4,h5,h6{font-weight:500}.lua-divider{background-color:#323232}.lua-divider.vertical{height:100%;width:1px}.lua-divider.horizontal{width:100%;height:1px}.lua-divider.margin{margin-top:10px;margin-bottom:10px}.lua-list{padding:7px;transition:all .2s}.lua-list>.lua-listitem-container{padding-bottom:0px;padding-top:0px;display:block;position:relative;width:100%}.lua-list>.lua-listitem-container.active{background:#323232}.lua-list.background{background-color:rgba(20,20,20,0.5);padding:0px;height:0px;overflow:hidden}.lua-list::-webkit-scrollbar{display:none}.lua-button{padding:7px 30px 7px 30px;width:100%;outline:none;border-radius:3px;background:transparent;border:none;color:white;transition:0.2s all;font-weight:400;font-size:15px;position:relative;overflow:hidden}.lua-button:hover{background:rgba(255,255,255,0.12)}.lua-button.active{background:#323232}.lua-ripple{width:100%}.lua-container{position:relative;color:white;padding:10px;padding-left:50px;max-height:100%;overflow-y:auto}.lua-container::-webkit-scrollbar{display:none}.lua-small{font-size:13px;color:white}.lua-info{font-size:13px;color:#4b4b4b}.lua-rows{display:flex;flex-direction:row;grid-gap:10px;gap:10px;margin-top:10px;border-radius:3px;padding:10px}.lua-column{display:flex;flex-direction:column;grid-gap:10px;gap:10px;margin-top:10px;border-radius:3px;padding:10px}.lua-card{background-color:#232323;border-radius:3px;display:flex;flex-direction:column;padding:10px;margin:0 auto;grid-gap:0;gap:0;flex-grow:1;flex-shrink:0;flex-basis:calc(50% - 10px)}.lua-card.fullwidth{width:100%}.lua-warning{display:block;background-color:rgba(255,0,0,0.3);color:white;padding:0.5rem;border-radius:3px;margin:10px;margin-left:3px;margin-right:3px}.lua-topbar{top:0;padding:5px;background-color:#2d2d2d;border-radius:3px;display:flex;flex-wrap:nowrap;align-items:center;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px}.lua-wrapper{display:flex;flex-direction:column;position:relative;color:white;padding:10px;padding-left:50px;max-height:100%;overflow-y:auto;width:calc(100% - 160px - 240px)}.lua-Inputbase{border-radius:3px;background-color:#101010;outline:none;border:none;color:white;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI Variable Text","Segoe UI","Meiryo",system-ui,ui-sans-serif,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji" !important;font-weight:400;position:relative;width:100%;padding:0;padding-top:7px;padding-bottom:7px}.lua-Inputbase.text{transition:all .1s}.lua-Inputbase.text:focus{background-color:#161616}.lua-Inputbase.button{cursor:pointer}.lua-Inputbase.button:hover{background-color:#131313}.lua-alert{position:fixed;width:100%;bottom:0;margin-bottom:25px;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:10px;gap:10px}.lua-alert>div{position:relative;background-color:#373737;display:flex;align-items:center;text-align:start;padding:7px;border-radius:4px;color:white;min-width:calc(100% / 7);font-weight:300}.lua-alert>div.success{background-color:#00a037}#lua-sample-container{position:relative}#lua-sample-container>img{width:100%;height:100%;-webkit-filter:blur(6px);filter:blur(6px)}#lua-sample-container>.lua-sample-title{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

